import React, { PureComponent } from 'react'
import { userInfo } from './Home'
import { Form, FormCell, Label, CellHeader, CellBody, CellsTitle } from 'react-weui'
import dayjs from 'dayjs'


export default class Detail extends PureComponent {
  constructor(props) {
    super(props)
    let obj = null
    if (this.props.location.query && this.props.location.query.id) {
      obj = userInfo.filter(item => item.id == this.props.location.query.id)[0]
    }
    
    this.state = {
      curTarget: obj,
      time: obj && obj.mList[0].date || '',
      money: obj && obj.mList[0].money || '',
      name: obj && obj.name || ''
    }
  }

  componentDidMount() {
    if (!this.state.curTarget) {
      this.props.history.replace({
        pathname: '/',
      })
    }
  }

  render() {
    const { time, money, name } = this.state
    let totalDays = Math.abs(dayjs(time).diff(dayjs(),'day')) + 1
    return (
      <div className="infoWrapper">
        <div>
          <div>起投日期: {time}</div>
          <div>持有人：{name}</div>
        </div>
        <div>
          <div className="infoTop">持有金额(元)：{money}</div>
          <div className="moreWrapper">
            <div className="moreItem">累计收益天数: <br /><span>{totalDays}</span><br />(包含当天{dayjs().format('YYYY/MM/DD')})</div>
            <div className="moreItem">利率： <br />万分之1.5</div>
            <div className="moreItem">累计收益(元)： <br /><span className="amount">{totalDays * (money / 10000) * 1.5}</span></div>
          </div>
        </div>
      </div>
    )
  }
}