import React, { PureComponent } from 'react'
import { Form, FormCell, Label, CellHeader, CellBody, Input, Button } from 'react-weui'

export const userInfo = [
  {
    name: '许兆宽',
    id: 'xzk',
    mList: [
      { type: 'add', money: '70000', date: '2021-10-07' },
    ]
  }
]

export default class Home extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      id: ''
    }
  }

  handleLogin = () => {
    const { id } = this.state

    const isHasId = userInfo.filter(item => item.id == id)[0]
    if (isHasId) {
      alert('登陆成功！')
      this.props.history.push({
        pathname: '/detail',
        query: {
          id
        }
      })
    } else {
      alert('登陆失败！')
    }
  }

  handleIdChange = (e) => {
    this.setState({
      id: e.target.value
    })
  }

  render() {
    return (
      <div className="loginWrapper">
        <div className="loginInner">
          <Form>
            <FormCell>
                <CellHeader>
                    <Label>用户ID</Label>
                </CellHeader>
                <CellBody>
                    <Input onChange={this.handleIdChange} type="tel" placeholder="请输入ID"/>
                </CellBody>
            </FormCell>
          </Form>
          <Button onClick={this.handleLogin} className="loginBtn" type="default" plain>登陆</Button>
        </div>
      </div>
    )
  }
}