import logo from './logo.svg';
import './App.css';
import {
  HashRouter as Router,
  Route,
  Switch
} from 'react-router-dom'
import Home from './pages/Home'
import Detail from './pages/Detail'

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/detail" component={Detail} />
      </Switch>
    </Router>
  );
}

export default App;
